@import url(https://fonts.googleapis.com/css?family=Lato:300,400|Poppins:300,400,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  background: #272727;
}

h1 {
  color: #CDCDCD;
  font-weight: normal;
}

h2 {
  color: #CDCDCD;
  font-weight: normal;
}
.about-container {
    justify-content: start;
    -webkit-animation: fadeInAnimation ease 1s;
            animation: fadeInAnimation ease 1s; 
    -webkit-animation-iteration-count: 1; 
            animation-iteration-count: 1; 
    -webkit-animation-fill-mode: forwards; 
            animation-fill-mode: forwards; 
}

.about-header {
    margin-bottom: 10px;
}
.about-wrapper {
    margin-left: 100px;
    margin-top: 20px;
}

.about-body {
    display:flex;
    flex-direction: row;
}

.about-body-left {
    max-width: 55%;
}

.about-intro {
    margin-bottom: 3rem;
}

.about-header {
    border-bottom: 2px solid #FF652F ;
    display: inline-block;
}

.about-work-header {
    border-bottom: 2px solid #14A76C;
    display: inline-block;
}

.about-body li {
    color: #A9A9A9;
    margin-top: 0.75rem;
    margin-bottom: 0.75rem;
}

.about-body a {
    text-decoration: none;
    color: #A9A9A9;
    transition: color 0.2s linear;
}
.about-body a:hover {
    color: #747474;
}

.about-body-left {
    padding-right: 50px;
}

.about-connect {
    margin-bottom: 3rem;
}

.about-connect-header {
    border-bottom: 2px solid #FFe400;
    display: inline-block
}

.icon-box {
    display: flex;
    align-items: center;
}


p {
    color: #A9A9A9;
    font-weight: 300;
    line-height: 25px;
    /* word-spacing: 3px; */
}



.picture {
    height: 600px;
    width: 400px;
    /* margin-left: 200px; */
    border-radius: 0.5rem;
    opacity: 1;
    transition: 0.5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    z-index: 1;
}

.middle {
    transition: 0.5s ease;
    opacity: 0;
    position: absolute;
    width: 100%;
    bottom: 0%;
    background: #272727;
    z-index: 0;
    height: 10%;
    vertical-align: middle;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    

}
.about-body-right-container {
    position: relative;
}

.text {
    color: #CDCDCD;
    text-align: center;
    padding-top: 1rem;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}

.about-body-right-container:hover .middle {
    opacity: 0.8;
  }
  
.icon {
    height: 20px;
    width: 20px;
    padding-right: 7px;
}

.about-body-right p {
    text-align: center;
}

.caption {
    padding-top: 6px;
}




@-webkit-keyframes fadeInAnimation { 
    0% { 
        opacity: 0; 
    } 
    100% { 
        opacity: 1; 
     } 
}




@keyframes fadeInAnimation { 
    0% { 
        opacity: 0; 
    } 
    100% { 
        opacity: 1; 
     } 
} 
* {
  margin: 0;
  padding: 0;
}

body, html {
  overflow: hidden;
}

.home-container {
  width: 100%;
  height: 70vh;
  background: #272727;
  display: flex;
  justify-content: center;
  align-items: center;
}
.home-container .box {
  width: 650px;
  height: 300px;
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.home-container .box .title {
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  height: 80px;
}
.home-container .box .title .block {
  width: 0%;
  height: inherit;
  background: #FF652F;
  position: absolute;
  -webkit-animation: mainBlock 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
          animation: mainBlock 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
  display: flex;
}
.home-container .box .title h1 {
  font-family: 'Poppins';
  color: #fff;
  font-size: 60px;
  -webkit-animation: mainFadeIn 2s forwards;
  animation: mainFadeIn 2s forwards;
  -webkit-animation-delay: 1.6s;
          animation-delay: 1.6s;
  opacity: 0;
  display: flex;
  align-items: baseline;
  position: relative;
}
.home-container .box .title h1 span {
  width: 0px;
  height: 0px;
  border-radius: 50%;
  background: #FFe400;
  -webkit-animation: load 0.6s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
  animation: popIn 0.8s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  margin-left: 5px;
  margin-top: -10px;
  position: absolute;
  bottom: 13px;
  right: -12px;
}
.home-container .box .role {
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  height: 30px;
  /* margin-top: -10px; */
}
.home-container .box .role .block {
  width: 0%;
  height: inherit;
  background: #14A76C;
  position: absolute;
  -webkit-animation: secBlock 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
          animation: secBlock 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  display: flex;
}
.home-container .box .role p {
  -webkit-animation: secFadeIn 2s forwards;
          animation: secFadeIn 2s forwards;
  -webkit-animation-delay: 3.2s;
          animation-delay: 3.2s;
  opacity: 0;
  font-weight: 400;
  font-family: 'Lato';
  color: #ffffff;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 5px;
}

@-webkit-keyframes mainBlock {
  0% {
    width: 0%;
    left: 0;
  }
  50% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0;
    left: 100%;
  }
}

@keyframes mainBlock {
  0% {
    width: 0%;
    left: 0;
  }
  50% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0;
    left: 100%;
  }
}
@-webkit-keyframes secBlock {
  0% {
    width: 0%;
    left: 0;
  }
  50% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0;
    left: 100%;
  }
}
@keyframes secBlock {
  0% {
    width: 0%;
    left: 0;
  }
  50% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0;
    left: 100%;
  }
}
@-webkit-keyframes mainFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes mainFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes popIn {
  0% {
    width: 0px;
    height: 0px;
    background: #e9d856;
    border: 0px solid #ddd;
    opacity: 0;
  }
  50% {
    width: 10px;
    height: 10px;
    background: #e9d856;
    opacity: 1;
    bottom: 45px;
  }
  65% {
    width: 7px;
    height: 7px;
    bottom: 0px;
    width: 15px;
  }
  80% {
    width: 10px;
    height: 10px;
    bottom: 20px;
  }
  100% {
    width: 7px;
    height: 7px;
    background: #e9d856;
    border: 0px solid #222;
    bottom: 13px;
  }
}
@keyframes popIn {
  0% {
    width: 0px;
    height: 0px;
    background: #e9d856;
    border: 0px solid #ddd;
    opacity: 0;
  }
  50% {
    width: 10px;
    height: 10px;
    background: #e9d856;
    opacity: 1;
    bottom: 45px;
  }
  65% {
    width: 7px;
    height: 7px;
    bottom: 0px;
    width: 15px;
  }
  80% {
    width: 10px;
    height: 10px;
    bottom: 20px;
  }
  100% {
    width: 7px;
    height: 7px;
    background: #e9d856;
    border: 0px solid #222;
    bottom: 13px;
  }
}
@-webkit-keyframes secFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}
@keyframes secFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}
footer {
  width: 350px;
  height: 80px;
  background: #ffb510;
  position: absolute;
  right: 0;
  bottom: -80px;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-animation: top 0.8s forwards;
          animation: top 0.8s forwards;
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}
footer span {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  color: #232323;
  font-family: 'Poppins';
}
footer span i {
  margin-right: 25px;
  font-size: 22px;
  color: #232323;
  -webkit-animation: icon 2s forwards;
          animation: icon 2s forwards;
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
  opacity: 0;
}

@-webkit-keyframes top {
  0% {
    opacity: 0;
    bottom: -80px;
  }
  100% {
    opacity: 1;
    bottom: 0px;
  }
}

@keyframes top {
  0% {
    opacity: 0;
    bottom: -80px;
  }
  100% {
    opacity: 1;
    bottom: 0px;
  }
}
@-webkit-keyframes icon {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  50% {
    opacity: 1;
    transform: scale(1.3) rotate(-2deg);
  }
  100% {
    opacity: 1;
    bottom: 0px;
  }
}
@keyframes icon {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  50% {
    opacity: 1;
    transform: scale(1.3) rotate(-2deg);
  }
  100% {
    opacity: 1;
    bottom: 0px;
  }
}

/* Add a black background color to the top navigation */
.topnav {
    overflow: hidden;
    padding-right: 2rem;
    background: #272727;
}

/* Style the links inside the navigation bar */
.topnav a {
    float: right;
    /* padding-right: 10px; */
    /* padding-left: 10px; */
    font-weight: bold;
    display: block;
    color: #747474;
    text-align: center;
    /* padding: 14px 16px; */
    padding-bottom: 6px;
    margin: 18px;
    text-decoration: none;
    font-size: 17px;
    border-bottom: 2px solid transparent;
    border-bottom-width: 20%;
    transition: border-bottom 0.6s linear;
}

.topnav a:hover {
    border-bottom: 2px solid black;  
}

.topnav a.home-inactive:hover {
    border-bottom: 2px solid #FF652F;
}

.topnav a.about-inactive:hover {
    border-bottom: 2px solid #14A76C;
}

.topnav a.portfolio-inactive:hover {
    border-bottom: 2px solid #FFe400;
}


.topnav a.home-active {
    border-bottom: 2px solid #FF652F;
}

.topnav a.about-active {
    border-bottom: 2px solid #14A76C;
}

.topnav a.portfolio-active {
    border-bottom: 2px solid #FFe400;
}

